.formPassive {
  display: none;
}

.formActive {
  grid-area: mc;
  position: fixed;
  width: 380px;
  top: 35%;
  left: 35%;
  font-size: 20px;
  text-align: center;
  background: #F0F0F0;
  opacity: 0.8;
  border: 2px solid #64E9EB;
  border-radius: 15px;
  z-index: 20;
  animation-name: formOpen;
  animation-duration: 1s;
}

.inputPass,
.inputName,
.submit {
  width: 300px;
  margin: 10px;
  font-size: 25px;
  text-align: center;
  background: lightblue;
  border-radius: 15px;
  border: none;
  outline: none;
}

.inputPass:focus,
.inputName:focus {
  background: #92f095;
}

.inputName {
  margin-left: -10px;
}

.formGreeting {
  font-size: 30px;
  color: #64E9EB;
}

.submit {
  color: white;
  background: #64E9EB;
}

.error {
  font-size: 25px;
  color: red;
}

.closeForm {
  width: 25px;
  position: relative;
  top: -100px;
  left: 86.8%;
  padding: 0;
  margin: 0;
  font-size: 30px;
  color: #64E9EB;
  border: none;
  background: #F0F0F0;
  border-radius: 15px;
  /* animation-name: formClose;
  animation-duration: 1s; */
}

.userGreeting {
  position: relative;
  height: 0;
  padding: 0;
  margin: 0;
  top: -88px;
  left: 91vw;
  font-size: 20px;
  color: white;
  text-decoration: none;
}

.userGreeting button {
  padding: 0;
  margin: 0;
  font-size: 18px;
  color: white;
  outline: none;
  border: 0;
  background-image: none;
  background: transparent;
  /* прозрачная кнопка */
}

@keyframes formOpen {
  from {
    left: 100%;
  }
  to {
    left: 35%;
  }
}


