@media screen and (max-width:768px) {
  .formActive {
    top: 25%;
    left: 25%;
  }
  @keyframes formOpen {
    from {
      left: 100%;
    }
    to {
      left: 25%;
    }
  }
}

@media screen and (max-width:425px) {
  .formActive {
    width: 300px;
    top: 25%;
    left: 14.5%;
  }
  @keyframes formOpen {
    from {
      left: 100%;
    }
    to {
      left: 14.5%;
    }
  }
  .inputPass,
  .inputName,
  .inputEmail,
  .submit {
    width: 290px;
    margin-left: 0;
  }
  .closeForm {
    top: -95px;
    left: 44.8%;
  }
}

@media screen and (max-width:375px) {
  .formActive {
    width: 300px;
    top: 25%;
    left: 9.5%;
  }
  @keyframes formOpen {
    from {
      left: 100%;
    }
    to {
      left: 9.5%;
    }
  }
}

@media screen and (max-width:320px) {
  .formActive {
    width: 300px;
    top: 25%;
    left: 2.5%;
  }
  @keyframes formOpen {
    from {
      left: 100%;
    }
    to {
      left: 2.5%;
    }
  }
}