.navbar {
  width: 90%;
  position: relative;
  display: flex;
  flex-direction: row;
  margin: 14rem auto;
  /* overflow: hidden; */
  /* Отображается только область внутри элемента, остальное будет скрыто.
						т.е дает отображаться отрисованному, скрывая часть header компоненты*/
  background: #333;
  opacity: 0.6;
}

.navbar a {
  float: left;
  font-size: 2rem;
  margin: 10px;
  color: white;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}

.dropdown {
  float: left;
  text-align: center;
  overflow: hidden;
  /* Отображается только область внутри элемента, остальное будет скрыто. */
}

.dropdown .dropbtn {
  margin: 10px;
  font-size: 2rem;
  border: none;
  outline: none;
  /* Одновременно устанавливает цвет, стиль и толщину 
					внешней границы на всех четырех сторонах элемента.
					Снимает синюю рамку с кнопки после нажатия  */
  /* color: white; */
  padding: 14px 16px;
  background-color: inherit;
  font-family: inherit;

}

.navbar a:hover,
.dropbtn:hover{
  color: #f750e9;
  transform: scale(1.1);
}
.navbar a,
.dropbtn{
  color: #f750e9;
}

.dropdownContent {
  display: none;
  /* временно удаляет элемент из документа */
  position: absolute;
  background: white;
  min-width: 160px;
  opacity: 1;
  z-index: 15;
  /* выводит элемент на передний план, 
			если отрицательное или меньше значение чем у предыдущего на задний  */
}

.dropdownContent a {
  float: none;
  /* не дает обтекать слева как в предыдущих */
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
  text-align: left;
}


.dropdown:hover .dropdownContent {
  display: block;
}


@media screen and (max-width:1200px){
  .navbar {
    flex-direction: column;
    margin: 2rem auto;
}
}

@media screen and (max-width:768px){
  .navbar {
    margin: 2rem auto;
  }
}




