.formPassive {
  display: none;
}

.formActive {
  grid-area: mc;
  position: fixed;
  top: 25%;
  left: 35%;
  width: 380px;
  font-size: 20px;
  text-align: center;
  background: #F0F0F0;
  opacity: 0.8;
  border: 2px solid #64E9EB;
  border-radius: 15px;
  z-index: 20;
  animation-name: formOpen;
  animation-duration: 1s;
}

.inputPass,
.inputName,
.inputEmail,
.submit {
  width: 300px;
  margin: 10px;
  font-size: 25px;
  background: lightblue;
  border-radius: 15px;
  border: none;
  outline: none;
  text-align: center;
}

.inputPass:focus,
.inputName:focus {
  background: #92f095;
}

.inputName {
  margin-left: -10px;
}

.formGreeting {
  font-size: 30px;
  color: #64E9EB;
}

.submit {
  color: white;
  background: #64E9EB;
}

.error {
  font-size: 25px;
  color: red;
}

.closeForm {
  width: 25px;
  position: relative;
  top: -100px;
  left: 84.8%;
  padding: 0;
  margin: 0;
  font-size: 30px;
  background: #F0F0F0;
  color: #64E9EB;
  border: none;
  border-radius: 15px;
  z-index: 21;
}

.userGreeting {
  position: relative;
  margin: 0 auto;
  bottom: 12%;
  /* left: 10%;  */
  width: 90%;
  font-size: 25px;
  line-height: 2.5rem;
  color: #2ea52a;
  opacity: 0.8;
  z-index: 60;
}

.userGreeting div {
  height: auto;
  padding: 20px;
  margin: 90px;
  background: white;
  border: 2px solid gray;
}

.userGreeting .closeForm {
  position: absolute;
  top:0;
  right: 0;
  color: #2ea52a;
  background: transparent;
}

@keyframes formOpen {
  from {
      left: 100%;
  }
  to {
    left: 35%;
  }
}

@media screen and (max-width:888px) {
  .closeForm {
    left: 82%;
  }
}

@media screen and (max-width:768px) {
  .formActive {
    top: 25%;
    left: 25%;
  }
  @keyframes formOpen {
    from {
        left: 100%;
    }
    to {
      left: 25%;
    }
  }

  .userGreeting { 
    font-size: 20px;
  }
  .closeForm {
    left: 72%;
  }
}

@media screen and (max-width:560px) {
  .userGreeting { 
    font-size: 16px;
  }
}

@media screen and (max-width:425px) {
  .formActive {
    width: 300px;
    top: 25%;
    left: 14.5%;
  }
  .inputPass,
  .inputName,
  .inputEmail,
  .submit {
    width: 290px;
    margin-left: 0;
  }
  .closeForm {
    top: -95px;
    left: 44.8%;
  }
  @keyframes formOpen {
    from {
        left: 100%;
    }
    to {
      left: 14.5%;
    }
  }

  .closeForm {
    top:0%;
    right: 0;
    left: 62%;
  }

}

@media screen and (max-width:375px) {
  .formActive {
    width: 300px;
    top: 25%;
    left: 9.5%;
  }
  @keyframes formOpen {
    from {
        left: 100%;
    }
    to {
      left: 9.5%;
    }
  }
}

@media screen and (max-width:320px) {
  .formActive {
    width: 300px;
    top: 25%;
    left: 2.5%;
  }
  @keyframes formOpen {
    from {
        left: 100%;
    }
    to {
      left: 2.5%;
    }
  }
}