.blog {
  grid-area: mc;
  width: 100%;
  height: 100%;
  margin-top: 2rem auto;
  padding: 0;
  position: relative;
  z-index: 10;
}

.blogPosts {
  position: relative;
  margin-left: 0%;
}

.userAva li {
  position: relative;
  top: -50px;
  left: -2rem;
  width: 65vw;
  min-height: 70px;
  color: #803e00;
  background: url(./img/background2.jpg);
}

.userAva .data {
  position: absolute;
  top: -25px;
  width: 95%;
  color: grey;
  text-align: right;
  padding-right: 5%;
}

.userAva img {
  width: 5rem;
  height: 5rem;
  position: relative;
  left: -30px;
  border-radius: 45px;
}

.inputForm h3 {
  background: white;
  width: auto;
  text-align: center;
}

.inputForm {
  width: 65vw;
  margin: 4rem auto;
  padding: 0;
  font-size: 20px;
  color: #803e00;
  -webkit-box-shadow: 12px 29px 81px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 12px 29px 81px 0px rgba(0, 0, 0, 0.75);
  box-shadow: 12px 29px 81px 0px rgba(0, 0, 0, 0.75);
}

.inputForm textarea {
  width: 99.5%;
  height: 100px;
  font-size: 20px;
  color: #803e00;
  border: none;
  outline: none;
  border-top: 2px solid #803e00;
  background: url(./img/background2.jpg) no-repeat;
  background-size: cover;
}

.inputForm h3 {
  font-size: 20px;
  color: #803e00;
  border-top: 2px solid #803e00;
  background: white;
}

.inputForm button {
  width: 50%;
  height: 35px;
  font-size: 20px;
  color: #803e00;
  border: 1px solid #803e00;
  border-top: 2px solid #803e00;
  border-bottom: 2px solid #803e00;
}

.inputForm button:hover {
  color: #66ff33;
  border: 3px solid #66ff33;
  background: white;
  transition: all .5s ease;
}

@media screen and(max-width:475px) {
  .userAva img {
    width: 45px;
    height: 45px;
  }
}