.form {
	position: relative;
	margin: 0 auto;
		
}
.form input {
	width:100%;
	height: 42px;
	padding-left: 10px;
	border: 2px solid #7BA7AB;
	border-radius: 5px;
	outline: none;
	background: #F9F0DA;
	color: #9E9C9C;
}

.form button {
	position: absolute; 
	top: 3px;
	right: 0px;
	width: 42px;
	height: 36px;
	border: none;
	background-image:  url(./lupa2.jpg);
	background-size:100%;
	border-radius: 0 5px 5px 0;
	cursor: pointer;
}
